import React from 'react';
import './About.css'

const about = props => {
    return (
        <div className="About">
            <h1>About Me</h1>
            <p>I am a web developer and I like to create challenging projects which involves frontend as well as backend.</p>
            <p>I want to create some cool web applications and wish to learn more and more every day.</p>
            <p>My main goal is to create end applications with a powerful backend which can serve as a large purpose for a large number of people.</p>
            <p>I want to learn Machine Learning in future and contribute in that domain too.</p>
        </div>
    )
}

export default about