import React from 'react'
import './SideDrawer.css'

const SideDrawer=props=>{
    let classes=[];
    if(props.show){
        classes.push("open");
    }else{
        classes.push("close");
    }

    console.log(classes.join(" "));
    

    return (
        <div className="SideDrawer">
            <div className="upper">
                <div className="Ham" onClick={props.hamClicked}>
                    <div className="Margin"></div>
                    <div className="Margin"></div>
                    <div className="Margin"></div>
                
                </div>
                <a href="https://github.com/niknikhil97" target="_blank"><i className="fab fa-github-square ic"></i></a>
            </div>
            
            <div className="lower">
                <ul className={classes.join(" ")}>
                    <li>Home</li>
                    <li>Projects</li>
                    <li>Contact</li>
                </ul>
            </div>
        </div>
    )
}

export default SideDrawer;

