import React , { Component }from 'react';
import './Full.css'

const Full = props => {

    let classes=["Full"]

    if(props.view===1){
        classes.push(" op-1 ")
    }else{
        classes.push(" op-2 ")
    }

    return (
        <div className={classes.join(" ")}>
            <div className="text-div">
                <h1>Welcome, I am Nikhil</h1>
                <h1>I am a Full Stack Developer</h1>
            </div>
            <div className="buttons-div">
                <button>About Me</button>
                <button>Skills</button>
                <h2>Or may be !</h2>
                <button onClick={props.changeView}>Change the view</button>
            </div>
        </div>
    )
}


export default Full;
